

    .card-page{
      padding-left: 9px;
      margin: auto;
      width: 100%;
      
    }

    .body-card{
      background-color: #cbd4fa;
      padding: 12px;
      border-radius: 25px 0 0 25px;
      border-color: #4e6cee;
      border-width: 2px;

    }

    .ready{
      padding: 100px 0;
      text-align: center; 
      margin: auto
    }

    .sub-card{
      margin: auto;
      text-align: center
    }

    .btn-solicitar{
      background-color: #ff735a; 
    }

    .btn-time{
      background-color: #4e6cee; 
    }
    
    

    .btn-solicitar, .btn-time{
      color: white;
      font-size: 40px;
      font-weight: 700;
      height: 150px;
      width: 220px;
    }
    
    .input-time-solicitar{
      background-color: #ff735a;
    }

    .input-time{
      background-color: #4e6cee;
    }

    .input-time-solicitar, .input-time{
      color: white;
      width: 220px;
      height: 50px;
			border-radius: 20px;
			text-align: center; 
      margin: auto;
			padding-top: 1px;
			padding-left: 15px;
			font-weight: bold;
			font-size: 40px;
			color: white;
			border: 0px;
    }

    


    .texto-assinado{
      font-size: 25px;
    }